import React, {Component} from 'react';
import Sectiontitle from '../component/Banner/Sectiontitle';

class Service extends Component{
    render(){
        var {wClass} = this.props
        return(
            <section className={`${wClass}`} id="service">
                <div className="container">
                    <Sectiontitle Title="CE FAC (CLAR, DIFERENIATOR)" TitleP="Ascult descrierea ta despre cealalt persoan,
                        identific tipologia ei comportamental i completez cu observaii bazate pe comportamentele prezentate.
                        Apoi i spun ce comportament s foloseti
                        i cum s-l aplici.
                         Nu vorbim despre tine.
                        Lucrm pe cellalt."/>
                </div>
            </section>
        )
    }
}

export default Service;