import React, {Component} from 'react';
import Reveal from 'react-reveal';
import WaterWave from 'react-water-wave';

class Banner extends Component {
    render(){
        let isacData = this.props.isacData;
        let {bClass, textLeft, imag}= this.props;
        return(
            <section id="home" >
                 <WaterWave strength={500} className={`banner_area ${bClass}`} style={{ width: '100%', height: '100%', backgroundSize: 'cover' }} imageUrl={require ("../../image/" + imag)}>
                        {({ getRootProps}) => (
                             <div className="container">
                                <div className={`banner_content ${textLeft}`}>
                                    <Reveal effect="fadeInUp">
                                        <h2>ISAC</h2>
                                    </Reveal>
                                    <Reveal  effect="fadeInUp" duration={1500}>
                                        <h2 className="wow fadeInLeft animated">{isacData.name}</h2>
                                    </Reveal>
                                    <Reveal effect="fadeInUp" duration={2200}>
                                        <h4 className="wow fadeInUp anoimated">Nu vorbim despre tine. Vorbim despre tipologia celuilalt.</h4>
                                        <h4 className="wow fadeInUp anoimated">Consultan comportamental. Decizie. Poziionare.</h4>
                                    </Reveal>
                                    <ul className="list_style social_icon">
                                    {
                                        isacData.socialLinks && isacData.socialLinks.map(item =>{
                                        return(
                                                <li key={item.name}>
                                                <a  href={item.url}><i className={item.className}></i></a> 
                                                </li>
                                                )
                                            }
                                        )
                                    }
                                    </ul>
                                </div>
                            </div>
                        )}
                </WaterWave>
            </section>
        );
    }
}

export default Banner;